.main-nav__toggle {
  position: absolute;
  left: 264px;
  top: 25px;
  background: transparent;
  width: 50px;
  height: 25px;
  cursor: pointer;
  border: none;
}
@media (min-width: 660px) {
  .main-nav__toggle {
    top: 45px;
    left: 600px;
  }
}
@media (min-width: 1200px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav__content--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 50px;
  height: 4px;
  background-color: #ffffff;
  box-shadow: 0 8px 0 0 #ffffff, 0 16px 0 0 #ffffff;
}
.main-nav__content--closed .main-nav__toggle:hover::before {
  background-color: #d22856;
  box-shadow: 0 8px 0 0 #d22856, 0 16px 0 0 #d22856;
}
.main-nav__content--closed .main-nav__toggle:active::before {
  background-color: rgba(210, 40, 86, 0.3);
  box-shadow: 0 8px 0 0 rgba(210, 40, 86, 0.3), 0 16px 0 0 rgba(210, 40, 86, 0.3);
}
.main-nav__content--opened .main-nav__toggle::before,
.main-nav__content--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  left: 8px;
  width: 34px;
  height: 3px;
  background-color: #ffffff;
}
.main-nav__content--opened .main-nav__toggle::before {
  transform: rotate(45deg);
}
.main-nav__content--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav__content--opened .main-nav__toggle:hover::after,
.main-nav__content--opened .main-nav__toggle:hover::before {
  background-color: #d22856;
}
.main-nav__content--opened .main-nav__toggle:active::after,
.main-nav__content--opened .main-nav__toggle:active::before {
  background-color: rgba(210, 40, 86, 0.3);
}
@media (max-width: 1200px) {
  .main-nav__content--closed .site-list__item {
    display: none;
  }
}
@media (max-width: 1200px) {
  .main-nav__content--closed .main-nav__header {
    background-color: rgba(40, 54, 69, 0.7);
    border-bottom: none;
  }
}
@media (max-width: 660px) {
  .main-nav__content--nojs .main-nav__toggle {
