.form {
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin: 0;
  background-color: #ffffff;
}

.form__title {
  .indent-reset();
  width: 280px;
  margin-top: 43px;
  margin-bottom: 34px;
  font-size: 14px;
  line-height: 30px;
  font-weight: 400;
}

.form__title span {
  display: none;
}

@media (min-width: @tablet-width) {
  .form__title span {
    display: inline-block;
  }
}

@media (min-width: @tablet-width) {
  .form__title {
    width: 490px;
    margin-top: 92px;
    margin-bottom: 70px;
    font-size: 18px;
  }
}

@media (min-width: @desktop-width) {
  .form__title {
    margin-bottom: 32px;
    width: 760px;
  }
}

.form__data {
  .indent-reset();
  display: flex;
  flex-direction: column;
}

@media (min-width: @tablet-width) {
  .form__data {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}

@media (min-width: @desktop-width) {
  .form__data {
    width: 940px;
    align-items: flex-end;
  }
}

.form__fullname {
  padding: 0 20px;
  margin-bottom: 22px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

@media (min-width: @tablet-width) {
  .form__fullname {
    margin: 0 auto;
    width: 620px;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    order: -3;
  }
}

@media (min-width: @desktop-width) {
  .form__fullname {
    padding: 0;
    margin: 0;
    margin-top: 52px;
    width: 433px;
    align-content: flex-end;
    align-items: flex-end;
  }
}

.form__field {
  padding-left: 21px;
  padding-top: 17px;
  padding-bottom: 11px;
  margin-bottom: 19px;
  width: 100%;
  box-sizing: border-box;

  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 18px;
  line-height: 30px;
  font-weight: 400;
  color: #283645;

  background-color: transparent;
  border: 2px solid #e5e5e5;
}

@media (min-width: @tablet-width) {
  .form__fullname .form__field {
    padding-bottom: 10px;
    margin-bottom: 54px;
    width: 525px;
    box-sizing: border-box;
  }
}

@media (min-width: @desktop-width) {
  .form__fullname .form__field {
    vertical-align: top;
    padding-bottom: 14px;
    margin-bottom: 0;
    margin-top: 50px;
    width: 341px;
  }

  .form__fullname .form__field--surname {
    margin-top: 0;
  }
}

@media (min-width: @tablet-width) {
  .form__field--name {
    margin-left: 20px;
    flex-grow: 1;
  }
}

@media (min-width: @tablet-width) {
  .form__fullname .form__field-title {
    margin-bottom: 50px;
    margin-left: 0;
  }
}

@media (min-width: @desktop-width) {
  .form__fullname .form__field-title {
    margin-bottom: 18px;
  }
}

.form__field-title {
  margin-left: 23px;
  margin-bottom: 5px;
  font-size: 14px;
  line-height: 30px;
  font-weight: 400;
  color: #d22856;
  text-transform: uppercase;
}

.form__progress {
  margin: 0 auto;
  box-sizing: border-box;
  width: 320px;
  padding-top: 120px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  flex-wrap: wrap;
  position: relative;
  border: none;
  background-color: #f2f2f2;
}

@media (min-width: @tablet-width) {
  .form__progress {
    padding: 0;
    margin: 0 auto;
    margin-top: 64px;
    padding-top: 56px;
    width: 620px;
    background-color: #ffffff;
    border: 2px solid #e5e5e5;
  }
}

@media (min-width: @desktop-width) {
  .form__progress {
    flex-direction: row;
    justify-content: space-between;
    padding: 0 60px;
    margin-top: 66px;
    padding-top: 55px;
    width: 940px;
    box-sizing: border-box;
  }
}

.form__titles {
  font-size: 14px;
  line-height: 18px;
  color: #d22856;
  text-transform: uppercase;
}

.form__titles--progress {
  position: absolute;
  left: 50%;
  top: 40px;
  margin-left: -113px;
  width: 226px;
}

@media (min-width: @tablet-width) {
  .form__titles--progress {
    width: 374px;
    margin-left: -219px;
    padding: 0 32px;
    top: -12px;
    background-color: #ffffff;
  }
}

.form__progress-name {
  position: relative;
  margin-left: 90px;
  margin-bottom: 51px;

  font-size: 18px;
  line-height: 30px;
  font-weight: 400;
  color: #283645;
}

@media (min-width: @tablet-width) {
  .form__progress-name {
    margin-bottom: 52px;
    margin-left: 120px;
  }
}

@media (min-width: @desktop-width) {
  .form__progress-name {
    margin-left: 60px;
  }
}

.form__progress-name::before {
  position: absolute;
  left: -70px;
  top: -4px;
  content: "";
  width: 39px;
  height: 39px;
  background-color: #ffffff;
  box-sizing: border-box;
  border: 2px solid #e5e5e5;
}

@media (min-width: @tablet-width) {
  .form__progress .form__progress-name::before {
    left: -62px;
  }
}

@media (min-width: @desktop-width) {
  .form__progress-name::before {
    left: -60px;
  }
}

@media (min-width: @desktop-width) {
  .form__progress-name--shark {
    order: 1;
  }

  .form__progress-name--chuck {
    margin-right: 0;
    order: 2;
  }

  .form__progress-name--souvenir {
    margin-bottom: 0;
    margin-right: -30px;
    order: 5;
  }

  .form__progress-name--tower {
    margin-right: 5px;
    order: 3;
  }

  .form__progress-name--hotel {
    margin-bottom: 60px;
    order: 6;
  }

  .form__progress-name--beach {
    margin-bottom: 0;
    order: 4;
  }
}

.form__progress-checkbox:checked + .form__progress-name::before {
  background-image: url("../img/icon-yes.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.form__contacts {
  margin-top: 30px;
  margin-bottom: 24px;
  padding: 0 20px;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  border: none;
}

@media (min-width: @tablet-width) {
  .form__contacts {
    padding: 0 60px;
    margin: 0 auto;
    margin-top: 65px;
    padding-top: 44px;
    width: 620px;
    background-color: #ffffff;
    border: 2px solid #e5e5e5;
    box-sizing: border-box;
    order: -1;
  }
}

@media (min-width: @desktop-width) {
  .form__contacts {
    padding-top: 42px;
    width: 940px;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
  }
}

.form__titles--contacts {
  display: none;
}

@media (min-width: @tablet-width) {
  .form__titles--contacts {
    display: block;
    position: absolute;
    left: 50%;
    margin-left: -140px;
    width: 280px;
    padding: 0 32px;
    box-sizing: border-box;
    top: -12px;
    background-color: #ffffff;
  }
}

@media (min-width: @tablet-width) {
  .form__field--phone {
    width: 440px;
    box-sizing: border-box;
    padding-left: 17px;
    padding-top: 16px;
    padding-bottom: 13px;
    margin-bottom: 9px;
    order: -1;
  }
}

@media (min-width: @tablet-width) {
  .form__field--phone {
    width: 440px;
    box-sizing: border-box;
    padding-left: 17px;
    padding-top: 16px;
    padding-bottom: 13px;
    margin-bottom: 9px;
    order: -1;
  }
}

@media (min-width: @desktop-width) {
  .form__field--phone {
    width: 315px;
  }
}

@media (min-width: @tablet-width) {
  .form__field--email {
    width: 440px;
    padding-left: 17px;
    padding-top: 16px;
    padding-bottom: 13px;
    margin-top: 26px;
    margin-bottom: 9px;
  }
}

@media (min-width: @desktop-width) {
  .form__field--email {
    padding-left: 20px;
    margin-top: 0;
    margin-right: 60px;
    margin-left: auto;
    width: 315px;
    order: -1;
  }
}

@media (min-width: @tablet-width) {
  .form__field-title--email {
    position: relative;
    order: 1;
    margin-bottom: 28px;
    margin-left: 17px;
  }
}

@media (min-width: @desktop-width) {
  .form__field-title--email {
    margin-bottom: 14px;
  }
}

@media (min-width: @tablet-width) {
  .form__field-title--phone {
    margin-left: 17px;
    position: relative;
  }
}

@media (min-width: @tablet-width) {
  .form__field-icon {
    position: absolute;
    left: 419px;
    top: -72px;
    width: 61px;
    height: 63px;

    background-image: url("../img/icon-mail.svg");
    background-color: #e5e5e5;
    background-repeat: no-repeat;
    background-position: center;
  }
}

@media (min-width: @tablet-width) {
  .form__field-icon--phone {
    background-image: url("../img/icon-phone.svg");
  }
}

@media (min-width: @desktop-width) {
  .form__field-icon {
    left: 292px;
  }
}

@media (min-width: @desktop-width) {
  .form__field-title--email {
    margin-left: auto;
    margin-right: 255px;
    width: 100px;
  }
}

.form__application {
  padding-top: 113px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  position: relative;
  border: none;
  background-color: #f2f2f2;
}

@media (min-width: @tablet-width) {
  .form__application {
    padding: 0;
    margin: 0 auto;
    margin-top: 15px;
    padding-top: 56px;
    width: 620px;
    background-color: #ffffff;
    border: 2px solid #e5e5e5;
    order: -2;
  }
}

@media (min-width: @desktop-width) {
  .form__application {
    margin: 0;
    padding-top: 58px;
    width: 435px;
    justify-content: flex-start;
    height: 300px;
    box-sizing: border-box;
  }
}

.form__titles--application {
  position: absolute;
  left: 50%;
  top: 37px;
  margin-left: -113px;
  width: 226px;
}

@media (min-width: @tablet-width) {
  .form__titles--application {
    width: 320px;
    margin-left: -184px;
    padding: 0 24px;
    top: -12px;
    background-color: #ffffff;
  }
}

.form__app-environ {
  position: relative;
  margin-left: 90px;
  margin-bottom: 45px;

  font-size: 18px;
  line-height: 30px;
  font-weight: 400;
  color: #283645;
}

@media (min-width: @tablet-width) {
  .form__app-environ {
    margin-left: 120px;
  }
}

@media (min-width: @desktop-width) {
  .form__app-environ {
    margin-left: 132px;
  }
}

@media (min-width: @tablet-width) {
  .form__app-environ-windows {
    margin-bottom: 58px;
  }
}

.form__app-environ::before {
  position: absolute;
  left: -72px;
  top: -4px;
  content: "";
  width: 39px;
  height: 39px;
  background-color: #ffffff;
  box-sizing: border-box;
  border: 2px solid #e5e5e5;
  border-radius: 50%;
}

.form__radio:checked + .form__app-environ::after {
  position: absolute;
  left: -60px;
  top: 8px;
  content: "";
  width: 15px;
  height: 15px;
  background-color: #d22856;
  box-sizing: border-box;
  border-radius: 50%;
}

@media (min-width: @tablet-width) {
  .form__app-environ::before {
    left: -59px;
  }

  .form__radio:checked + .form__app-environ::after {
    left: -47px;
  }
}

.form__emotions {
  .indent-reset();
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  position: relative;
  border: none;

}

@media (min-width: @tablet-width) {
  .form__emotions {
    padding: 0;
    margin: 0 auto;
    margin-top: 67px;
    padding-top: 45px;
    padding-bottom: 51px;
    width: 620px;
    background-color: #ffffff;
    border: 2px solid #e5e5e5;
  }
}

@media (min-width: @desktop-width) {
  .form__emotions {
    padding: 0 60px;
    margin-top: 65px;
    padding-top: 41px;
    padding-bottom: 41px;
    width: 940px;
    box-sizing: border-box;
  }
}

.form__titles--emotions {
  margin: 0 auto;
  margin-top: 37px;
  margin-bottom: 15px;
}

@media (min-width: @tablet-width) {
  .form__titles--emotions {
    position: absolute;
    width: 262px;
    left: 50%;
    margin-left: -131px;
    padding: 0 24px;
    top: -50px;
    background-color: #ffffff;
    box-sizing: border-box;
  }
}

.form__field--emotions {
  width: 280px;
  min-height: 202px;
  padding-left: 20px;
  padding-right: 20px;
  resize: none;
  font-family: inherit;

}

@media (min-width: @tablet-width) {
  .form__field--emotions {
    padding-top: 13px;
    margin: 0 auto;
    width: 500px;
    min-height: 190px;
  }
}

@media (min-width: @desktop-width) {
  .form__field--emotions {
    padding-left: 16px;
    padding-top: 15px;
    width: 100%;
    min-height: 130px;
  }
}

.form__send {
  margin: 0 auto;
  margin-top: 31px;
  width: 280px;
  height: 63px;

  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 18px;
  line-height: 30px;
  font-weight: 700;
  color: #ffffff;
  text-transform: uppercase;

  background-color: #2aab6d;
  border: none;
}

@media (min-width: @tablet-width) {
  .form__send {
    margin: 0;
    margin-top: 68px;
    margin-left: 20px;
    margin-bottom: 50px;
    width: 300px;
  }
}

@media (min-width: @desktop-width) {
  .form__send {
    margin-top: 67px;
    margin-left: 0;
    width: 300px;
  }
}

.form__necessarily {
  position: relative;
  margin: 0 auto;
  display: flex;
  margin-top: 33px;
  margin-bottom: 18px;
  margin-left: 72px;
  font-size: 14px;
  line-height: 30px;
  text-transform: uppercase;

  &::before {
    position: absolute;
    left: 7px;
    top: 18px;
    content: "";
    width: 15px;
    height: 1px;
    background-color: #000000;
  }

  sup {
    margin-right: 19px;
    color: #d22856;
  }

  span {
    display: none;
  }
}

@media (min-width: @tablet-width) {
  .form__necessarily {
    margin-right: 20px;
    margin-top: 86px;
  }
}

@media (min-width: @desktop-width) {
  .form__necessarily {
    align-self: flex-start;
    margin-right: 0;
    margin-top: 83px;

    span {
      display: inline-block;
    }
  }
}
